body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header {
  box-sizing: border-box;
  width: 100%;
  padding-left: 10px;
}

.header h3 {
  margin: 0;
}

#Editor-header {
  background: white;
  color: black;
}

#Preview-header {
  background: rgb(49, 49, 49);
  color: rgb(205, 225, 231);
}

#editor {
  box-sizing: border-box;
  padding: 10px 20px;
  width: 100%;
  height: 300px;
  border: 0;
  resize: none;
  overflow-y: auto;
  background: rgb(37, 37, 37);
  color: white;
}

#preview {
  padding: 0 20px;
  height: 360px;
  overflow-y: auto;
  background: rgb(214, 212, 204);
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

#preview img {
  width: 100px;
}

#preview pre {
  background: white;
  padding: 5px;
}

#preview code {
  background: white;
  padding: 1px 4px 2px 4px;
  font-size: 12px;
  font-weight: bold;
  font-family: monospace;
}

#preview blockquote {
  border-left: 5px solid rgb(28, 188, 241);
  -webkit-margin-before: 1em;
          margin-block-start: 1em;
  -webkit-margin-after: 1em;
          margin-block-end: 1em;
  -webkit-padding-start: 20px;
          padding-inline-start: 20px;
  -webkit-padding-end: 20px;
          padding-inline-end: 20px;
  -webkit-margin-start: 20px;
          margin-inline-start: 20px;
  -webkit-margin-end: 20px;
          margin-inline-end: 20px;
}

#preview cite {
  margin-left: 1.5em;
}

#preview table {
  border: 3px solid black;
  border-radius: 10px;
  background: white;
  padding: 5px;
}

#preview thead th {
  border-bottom: 2px solid;
}

#preview td {
  border: 1px solid black;
  padding: 5px;
}

footer {
  text-align: center;
  font-size: 20px;
  padding: 10px;
  color: white;
}

footer a {
  text-decoration: none;
  color: rgb(230, 55, 55);
  font-weight: bold;
}

footer a:hover {
  text-decoration: underline;
}
.App {
  background: rgb(57, 76, 131);
  padding-top: 20px;
}

.editor-window,
.preview-window {
  box-sizing: border-box;
  width: 50%;
  margin: auto;
  border: 3px solid rgb(201, 201, 201);
}

.editor-window {
  margin-bottom: 20px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.preview-window {
  width: 60%;
}

@media only screen and (max-width: 800px) {
  .editor-window,
  .preview-window {
    width: 95%;
    /* margin: 0; */
  }
}

